<template>
    <div class="main-content">
        <div class="breadcrumbs" id="breadcrumbs">
            <ul class="breadcrumb">
                <li>
                    <i class="icon-home home-icon">
                    </i>
                    <a href="#">
                    </a>
                </li>
                <li class="active">
                    <router-link to="/whd">
                        微互动
                    </router-link>
                </li>
            </ul>
        </div>
        <div class="page-content ha_page_content">
            <div class="el-row contBox clear">
                <div class="cs-top">
                    <el-col :span="6">
                        <el-button :plain="is_xz" @click="wfbClick($event)" type="primary">
                            已发布
                        </el-button>
                        <el-button :plain="!is_xz" @click="yfbClick($event)" type="primary">
                            未发布
                        </el-button>
                    </el-col>
                    <el-col :offset="14" :span="4">
                        <el-input :on-icon-click="handleIconClick" icon="search" v-model="input2">
                        </el-input>
                    </el-col>
                </div>
                <div class="cs-list-table">
                    <!-- 列表 -->
                    <el-table :data="tableData" border="" style="width: 100%;">
                        <el-table-column label="序号" prop="" width="100">
                        </el-table-column>
                        <el-table-column label="提出人" prop="">
                        </el-table-column>
                        <el-table-column label="提出时间" prop="">
                        </el-table-column>
                        <el-table-column label="咨询问题" prop="">
                        </el-table-column>
                        <el-table-column label="回复状态" prop="">
                        </el-table-column>
                        <el-table-column label="回复人" prop="">
                        </el-table-column>
                        <el-table-column label="回复时间" prop="">
                        </el-table-column>
                        <el-table-column label="操作" width="180">
                            <template scope="scope">
                                <el-button @click="handleClick" size="small" type="text">
                                    查看
                                </el-button>
                                <el-button @click="deletClick" size="small" type="text">
                                    删除
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-pagination :total="50" layout="prev, pager, next">
                    </el-pagination>
                </div>
            </div>
        </div>
        <zxfwcomponent v-if="is_hfshow">
        </zxfwcomponent>
        <tankuangcomponent :content="content" v-if="is_tkshow">
        </tankuangcomponent>
    </div>
</template>
<script>
    export default {
    methods: {
      handleClick() {
        this.is_hfshow=true;
      },
      deletClick(){
        this.is_tkshow=true;
          this.content={
            button:"回复",
            message:"注意",
            fn:function(){
                var Bus=global.eventHub;
                Bus.$emit("delet");
            }
         }
      }
    },
    components: {
        zxfwcomponent : function (resolve, reject) {
            require(['./zxfw_hf.vue'],  resolve)
        },
        tankuangcomponent:function (resolve, reject) {
            require(['./tankuang.vue'],  resolve)
        }
    },
    data() {
      return {
        tableData: [{
          date: '2016-05-03',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }],
        is_hfshow:false,
        is_tkshow:false,
        content:{},
      }
    },
    created(){
      var that=this;
      var Bus=global.eventHub;
      Bus.$on("qrclick",function(){
        that.is_hfshow=false;
      });
      Bus.$on("delet",function(){
        that.is_tkshow=false;
      });
    }
  }
</script>
<style scoped="">
    .cs-top{
  overflow: hidden;
}
.cs-top .el-col{
    padding: 20px 0;
    line-height: 36px;
}
</style>


